@import '../../styles/settings.scss';

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 4.3rem;
  background-color: $mediumblue;
  border-radius: 1rem;
  min-width: 6rem;
  height: 60rem;
  padding-top: 2.213rem;
}

.navbar__interactive {
  display: flex;
  flex-direction: column;
}

.navbar__icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2.3rem;
  min-width: 1.25rem;
  min-height: 1.25rem;

  img {
    cursor: pointer;

    &:hover {
      filter: hue-rotate(130deg) brightness(1.5);
    }
  }
}

.navbar__avatar {
  text-align: center;
  margin-top: 29.8rem;

  img {
    height: 2.5;
    width: 2.5rem;
    border: 0.1rem solid $white;
    border-radius: 50%;
  }
}

.linkActive {
  filter: brightness(3);
}

.avatarActive {
  filter: brightness(1.5);
}

// -------------------------- MEDIA -------------------------------

@media screen and (min-width: 520px) and (max-width: 1024px) {

  .navbar, .navbar__interactive, .navbar__icons {
    flex-direction: row;
    padding: 0;
  }

  .navbar {
    padding: 1.563rem;
    gap: 0;
    height: 4.5rem;
    width: 100%;
    justify-content: space-between;
  }

  .navbar__icons {
    gap: 2rem;
  }

  .navbar__interactive {
    gap: 0;
  }

  .navbar__avatar {
    margin-top: 0;
  }
}

@media screen and (max-width: 519px) {

  .navbar, .navbar__interactive, .navbar__icons {
    flex-direction: row;
    padding: 0;
  }

  .navbar {
    border-radius: 0;
    padding: 1.125rem 1rem;
    gap: 0;
    height: 3.5rem;
    width: 100%;
    justify-content: space-between;
  }

  .navbar__icons {
    min-width: 1rem;
    min-height: 1rem;
    gap: 1.5rem;
  }

  .navbar__interactive {
    gap: 0;
  }

  .navbar__avatar {
    margin-top: 0;
  }

  .navbar__avatar img {
    max-width: 2rem !important;
    max-height: 2rem !important;
  }
}
