.tvseries__list__wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 2.5rem;
}

.tvseries {
  h1 {
    margin-bottom: 2.375rem;
  }
}

// -------------------------- MEDIA -------------------------------

@media screen and (min-width: 520px) and (max-width: 1024px) {

  .tvseries__list__wrapper {
    gap: 1.3rem;
    justify-content: center;
  }
}

@media screen and (max-width: 519px) {

  .tvseries__list__wrapper {
    gap: 1rem;
    justify-content: center;
  }

  .tvseries h1 {
    font-size: 1.25rem;
    margin-bottom: 1.5rem;
  }
}
