@import '../../styles/settings.scss';

.btn {
  width: 21rem;
  height: 3rem;
  border: none;
  border-radius: 0.5rem;
  background-color: $red;
  color: $white;
  font-family: $font-text;
  cursor: pointer;
  padding-bottom: 0.2rem;
}

@media screen and (max-width: 480px) {
  .btn {
    max-width: 16.5rem;
  }
}
